import React, { useEffect, memo } from "react";
import Title from "./Title.js";
import "./ProjectOverlay.css";
import ReactMarkdown from "react-markdown";
import { ReactComponent as CloseIcon } from "../images/cross.svg";
import { MetaTags } from "react-meta-tags";

function ProjectOverlay({ close, infoRef, infoStyle, onAnimationEnd, project }) {
  useEffect(() => {
    document.documentElement.style.setProperty("--blur-amount", 2);

    return () => {
      document.documentElement.style.setProperty("--blur-amount", 0);
    };
  }, []);

  return (
    <div className="grid lg:grid-cols-4 project-overlay">
      <MetaTags>
        <title>{project.title}</title>
        <meta name="description" content={project.description} id="project-description" />
        <meta property="og:title" content={project.name} id="project-title" />
        <meta property="og:image" content={project.previewImage ?? project.images ? project.images[0].url : "/images/Julian.png"} id="project-image" />
      </MetaTags>
      <div
        className="project-blur hidden lg:flex"
        onClick={() => close()}
      ></div>
      <div className="project-info col-span-3 shadow-xl" onAnimationEnd={onAnimationEnd} ref={infoRef} style={infoStyle}>
        <div className="flex justify-end sm:hidden">
          <button onClick={() => close()}>
            <CloseIcon style={{ margin: 20 }} className="close-svg" />
          </button>
        </div>
        <div>
          <div className="flex sm:justify-between items-center m-4 sm:m-8">
            <Title title={project.title} />
            <button className="hidden sm:flex" onClick={() => close()}>
              <CloseIcon style={{ margin: 20 }} />
            </button>
          </div>
          {/* Content */}
          <div className="grid grid-cols-1 sm:grid-cols-3 gap-16 mx-4 sm:mx-8">
            <p className="sm:col-span-2">
              <ReactMarkdown>{project.description}</ReactMarkdown>
            </p>
            {/* Meta data */}
            <div className="flex flex-col">
              {project.metaData !== undefined ? (
                project.metaData.map((meta, i) => (
                  <ReactMarkdown key={i}>{meta}</ReactMarkdown>
                ))
              ) : (
                <></>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default memo(ProjectOverlay);
